// $import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";


$screen-xs:                  480px;
$screen-xs-min:              $screen-xs;
$screen-phone:               $screen-xs-min;

$screen-sm:                  768px;
$screen-sm-min:              $screen-sm;
$screen-tablet:              $screen-sm-min;

$screen-md:                  992px;
$screen-md-min:              $screen-md;
$screen-desktop:             $screen-md-min;

$screen-lg:                  1200px;
$screen-lg-min:              $screen-lg;
$screen-lg-desktop:          $screen-lg-min;

$screen-xs-max:              ($screen-sm-min - 1);
$screen-sm-max:              ($screen-md-min - 1);
$screen-md-max:              ($screen-lg-min - 1);

$color: #fff;
$color-invert: #444;
$accent: #FF5722;
$bg: #fff;
$highlight-bg: #eaeaea;

$footer-height: 78px;

body, html {
    background-color: #1B1B37;
    background-image: url('../img/head.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
}

hr {
    border-color: #DDD;
}

#main-container {
    height: 100%;
}

#main-content-container {
    margin: 16px;
    margin-bottom: 0;
    padding-bottom: $footer-height + 16px;
}

#main-sidebar {
    padding-top: 180px - 64px;
    color: white;
    @media (max-width: $screen-sm-min) {
          padding-top: 0;
    }
    a {
        color: white;
    }
}

#main-row {
    padding-top: 64px;
    box-sizing: border-box;
    height: 100%;
    @media (max-width: $screen-sm-min) {
        padding-top: 24px;
    }
}

#main-logo {
    width: 90%;
    @media (max-width: $screen-sm-min) {
        width: 60%;
    }
}

.container-fluid #main-logo {
    width: 75%;
}

#main-page {
    height: 100%;
}

#main-content {
    background-color: #fff;
    min-height: 100%;
    position: relative;
}

#passwordViewer {
    background-color: #ddd;
    border: none;
}

#passwordViewer:active {
    background-color: #bbb;
}

#footer {
    height: $footer-height;
    padding: 16px;
    background-color: $highlight-bg;
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.breadcrumb {
    margin-bottom: 0;
    background-color: $highlight-bg;
}

.sidebar-ul {
    list-style-type: none;
    padding: 0;
    a {
        text-decoration: none;
    }
}

.display-user {
    color: white;
    .username-group{
        font-size: 18px;
        .rank {
            color: #aaa;
        }
    }
}

.user-card {
    background-color: #eee;
    background-size: cover;
    color: $color-invert;
    margin-bottom: 16px;
    .user-card-content {
        padding: 16px;
        h1, h2, h3, h4, h5, h6 {
            color: $color-invert;
            small {
                color: lighten($color-invert, 10%);
            }
        }
        ul {
            list-style-type: none;
            padding: 0;
            margin: 0;
            li {
                label {
                    font-size: 14px;
                    color: lighten($color-invert, 30%);
                }
                font-size: 18px;
            }
        }
    }
}

.attendance-checking {
    .status-show-psuedo-style {
        padding: 6px 0px 6px 0px;
        font-size: 16px;
        line-height: 24px;
        font-family: Roboto, 'Helvetica Neue', Helvetica, Arial, sans-serif;
    }
    .last-check {
        margin: 0;
    }
    .last-check-1 {
        font-size: 18px;
    }
    .last-check-2 {
        font-size: 16px;
    }
    .last-check-3 {
        font-size: 14px;
    }
    .small-last-check {
        font-size: 10px; margin: 0;
    }
    .local-name {
        margin-bottom: 0;
    }
    .local-status {

    }
    .user-attendance-card {
        width: 240px;
        height: 180px;
        float: left;
        padding: 6px;
        cursor: pointer;
        .user-attendance-card-inner {
            width: 100%;
            height: 100%;
            background-color: #aaa;
            background-size: cover;
            position: relative;
            transition: opacity 0.25s, transform 0.25s;
            .user-attendance-card-gradiant{
                width: 100%;
                height: 100%;
                background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(207,207,207,0) 19%,rgba(0,0,0,0.75) 100%);
                padding: 8px;
                .user-attendance-card-content {
                    color: white;
                    position: absolute;
                    bottom: 8px;
                    width: 240px - 2*(6px) - 2*(8px);
                    > .nickname {
                        margin: 0;
                        font-size: 28px;
                        color: white;
                        > small {
                            color: white;
                        }
                    }
                    > .camp_id {
                        margin: 0;
                        font-size: 16px;
                        color: white;
                    }
                    > hr {
                        margin: 6px 0 6px 0;
                    }
                    > p {
                        margin: 0;
                        font-size: 14px;
                        line-height: 16px;
                    }
                    > .sec {
                        float: right;
                    }
                }
            }
        }
        .user-attendance-card-selected {
            opacity: 0.60;
            transform: scale(0.9);
            transition: opacity 0.25s, transform 0.25s;
        }
    }
}

.col-no-padding {
    padding: 0;
}

.col-registration {
    padding: 2px;
    .panel {
        box-shadow: none;
        border: 1px solid #ddd;
    }
}

.user-card:last-child {
    margin-bottom: 0px;
}
